import React from 'react';
import my_face from '../images/me_joker.jpg';
import ScrollAnimation from 'react-animate-on-scroll';

const About = () => {
    return (
        <div className="about-bg" id="about">
            <div className="about">
                <div className="about-me">
                    <ScrollAnimation animateIn="animate__fadeInLeft">
                        <p className="about-me-p">
                            Hi, I am Shreyas Vijay Ghansawant,<br /> I am 17 years old.<br />
                            This is my portfolio website,<br /> In this site I have showcased <br />
                            projects/websites created by me.<br /> This is my first website using ReactJS.<br />
                        </p>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn="animate__flipInX">
                        <div className="skills">
                            <h2>Skills</h2>
                            <div class="skills-ul">
                                <div>
                                    <b>HTML5</b>
                                </div>
                                <div>
                                    <b>CSS3</b>
                                </div>
                                <div>
                                    <b>JavaScript</b><br />jQuery, Ajax, React(Learning)
                                </div>
                                <div>
                                    <b>Python</b><br /> Django(Web Framework)
                                </div>
                            </div>
                        </div>
                    </ScrollAnimation>
                </div>
                <div className="my-picture">
                    <ScrollAnimation animateIn="animate__zoomIn">
                        <img src={my_face} width="70%" alt="My Image With Joker" />
                    </ScrollAnimation>
                </div>
            </div>
        </div>
    )
}

export default About