import React from 'react';
import twitter from '../images/twitter.png';
import ig from '../images/ig.png';
import github from '../images/github.png';
import reddit from '../images/reddit.png';
import ScrollAnimation from 'react-animate-on-scroll';

const Contact = () => {
    return (
        <div className="contact-div" id="contact">
            <h4>Contact</h4>
            <ScrollAnimation animateIn="animate__zoomIn">
                <div className="contact">
                    <div className="contact-info">
                        <span>Name: Shreyas Vijay Ghansawant</span><br />
                        <span>DOB: 28th March 2003</span><br />
                        <span>Email: <a>shreyasghansawant@gmail.com</a></span><br />
                        <span>Phone: 7620979186</span><br />
                    </div>
                    <div className="social">
                        <a href="#">
                            <img src={twitter} alt="Twitter Logo" />
                        </a>
                        <a href="#">
                            <img src={ig} id="invert" alt="Instagram Logo" />
                        </a>
                        <a href="#">
                            <img src={github} alt="Github Logo" />
                        </a>
                        <a href="#">
                            <img src={reddit} alt="Reddit Logo" />
                        </a>
                    </div>
                </div>
            </ScrollAnimation>
        </div>
    )
}

export default Contact